<template>
    <div class="app">
        <div class="cashBox">
            <div class="cashWhiteBox">
                <p class="imgBox"><img :src="cashInfo.icon" alt=""></p>
                <p class="cashCur">{{cashInfo.name}}</p>
                <p class="cashName">{{cashInfo.tag}}</p>
                <p class="balance">可提现</p>
                <p class="balanceNum">{{cashInfo.money}}</p>
                <div class="numInputBox">
                    <input v-model="cash_money" type="number" placeholder="请输入提现数量">
                    <p><span class="numCashCur">{{cashInfo.name}}</span><span @click="getAllMoney" class="allBtn">全部</span></p>
                </div>
                <div class="address"><input v-model="cashForm.address" type="text" placeholder="输入或者粘贴地址"></div>
                <p class="serverCharge"><span class="charge">手续费：{{serviceMoney}}</span><span class="actual">实际到账：{{realMoney}}</span></p>
                <button @click="cashSubmit" :disabled="cashLoading">提现</button>
            </div>
        </div>
    </div>
</template>

<script>
    import {cashInfo, cash} from "@/api/user";
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';
    import fmath from '@/utils/math'
    export default {
        name: "withdraw",
        data() {
            return {
                cashLoading:false,
                // 提现信息
                cashInfo:{

                    money:0,
                    icon:'',
                    name:'',
                    tag:'',
                    cash_service_ratio: 0,
                    cash_service_max: 0,
                    cash_min: 0,
                    cash_max: 0,
                },
                // 提现表单
                cashForm:{
                    cash_money:'',
                    address:'',
                    currency_id:''
                },
                serviceMoney:0,
                realMoney:0,
                cash_money:''
            }
        },
        watch:
        {
            // 计算手续费及到账费用
            cash_money(val)
            {
                if (val > this.cashInfo.money)
                {
                    this.cash_money = this.cashInfo.money
                }
               let ratio = this.cashInfo.cash_service_ratio / 100
               let smoney = fmath.floatMul(ratio, this.cash_money)
                if (this.cashInfo.cash_service_max > 0 && smoney > this.cashInfo.cash_service_max)
                {
                    this.serviceMoney = this.cashInfo.cash_service_max
                } else
                {
                    this.serviceMoney = smoney
                }

                this.realMoney = fmath.floatSub(this.cash_money, this.serviceMoney)
            }
        },
        mounted()
        {
            this.currency_id = this.$route.query.currencyId
            this.getCashInfo()
        },
        methods:{

            // 获取提现信息
            getCashInfo()
            {
                cashInfo(this.currency_id).then(res => {

                    this.cashInfo = res
                })
            },
            // 全部
            getAllMoney()
            {
               this.cash_money = this.cashInfo.money
            },
            // 提现
            cashSubmit()
            {
                if (this.cash_money == 0 || this.cash_money == '')
                {
                    Toast('请输入提现金额')
                    return
                }
                if (this.cashInfo.cash_max > 0 && this.cash_money > this.cashInfo.cash_max)
                {
                    Toast('当前最高提现额度为:'+this.cashInfo.cash_max)
                    return
                }
                if (this.cashForm.address == '')
                {
                    Toast('请输入提现地址')
                    return
                }
                this.cashForm.cash_money = this.cash_money
                this.cashForm.currency_id = this.currency_id
                this.cashLoading = true

                cash(this.cashForm).then(res => {

                    this.cashLoading = false
                    this.cashInfo.money = fmath.floatSub(this.cashInfo.money, this.cash_money);
                    Dialog.alert({
                        message: '已成功申请提现'
                    }).then(() => {
                        // on close

                    });

                })
            }
        }
    }
</script>

<style scoped>
    @import "../../../static/css/withdraw.css";
</style>



